import * as React from 'react';
import CircleIcon from '@mui/icons-material/Circle';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';

interface IMistakesProps {
 numMistakesLeft: number;
}

export default function Mistakes(props: IMistakesProps) {
    const { numMistakesLeft } = props;
    return (
        <>
        <Typography align="center" variant="body1" fontSize={11} sx={{marginTop: 5, display:"flex", justifyContent:"center", color: "rgba(0, 0, 0, 0.54)"}}>
            Mistakes Remaining
          </Typography>
        <Stack direction="row" spacing={1} sx={{marginTop: 2, display:"flex", justifyContent:"center"}}>
        {Array.from(Array(numMistakesLeft)).map((_, index) => <CircleIcon sx={{color: "indianred"}}></CircleIcon>)}
        </Stack>
        </>
    );
}