import * as React from 'react';
import { ITriviaQuestion, ITriviaAnswer, ITriviaCategory, TriviaSet, TriviaType } from '../data/Trivia';
import Buttons from './trivia-game-components/Buttons'; 
import Confetti from 'react-confetti';
import {useWindowSize} from 'react-use';
import TriviaCategory from './trivia-game-components/TriviaCategory';
import TriviaQuestion from './trivia-game-components/TriviaQuestion';
import TriviaAnswer from './trivia-game-components/TriviaAnswer';
import TriviaScore from './trivia-game-components/TriviaScore';
import { IconButton, Stack, Typography } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';

export interface ITriviaGameProps {
    onClickHome: () => void;
};

interface IGameData {
    currentId: string;
    team1Points: string;
    team2Points: string;
    hasWon: boolean;
};

export default function TriviaGame(props: ITriviaGameProps) {
    const { onClickHome } = props;
    const {width, height} = useWindowSize();
    const localInitialId = localStorage.getItem("currentId");
    const localInitialTeam1Points = localStorage.getItem("team1Points");
    const localInitialTeam2Points = localStorage.getItem("team2Points");

    const initialId = localInitialId && Number(localInitialId) <= TriviaSet.size ? localInitialId : "1";
    const initialTeam1Points = localInitialTeam1Points && localInitialId && Number(localInitialId) <= TriviaSet.size ? localInitialTeam1Points : "0";
    const initialTeam2Points = localInitialTeam2Points && localInitialId && Number(localInitialId) <= TriviaSet.size ? localInitialTeam2Points : "0";

    localStorage.setItem("currentId", initialId);
    localStorage.setItem("team1Points", initialTeam1Points);
    localStorage.setItem("team2Points", initialTeam2Points);

    const [gameData, setGameData] = React.useState<IGameData>({
        currentId: initialId,
        team1Points: initialTeam1Points,
        team2Points: initialTeam2Points,
        hasWon: false,
    })

    const onClickAddPoints = (team1Points: string, team2Points: string) => {
        const newTeam1Points = String(Number(gameData.team1Points) + Number(team1Points));
        const newTeam2Points = String(Number(gameData.team2Points) + Number(team2Points));
        localStorage.setItem("team1Points", newTeam1Points);
        localStorage.setItem("team2Points", newTeam2Points);
        setGameData({
            currentId: gameData.currentId,
            team1Points: newTeam1Points,
            team2Points: newTeam2Points,
            hasWon: Number(gameData.currentId) > TriviaSet.size,
        });
    };

    const onClickNext = () => {
        const newCurrentId = String(Number(gameData.currentId) + 1);
        localStorage.setItem("currentId", newCurrentId);
        setGameData({
            currentId: newCurrentId,
            team1Points: gameData.team1Points,
            team2Points: gameData.team2Points,
            hasWon: Number(gameData.currentId) >= TriviaSet.size,
        })
    }

    const onClickBack = () => {
        const newCurrentId = String(Number(gameData.currentId) - 1);
        localStorage.setItem("currentId", newCurrentId);
        setGameData({
            currentId: newCurrentId,
            team1Points: gameData.team1Points,
            team2Points: gameData.team2Points,
            hasWon: Number(gameData.currentId) > TriviaSet.size,
        })
    }

    const currentTriviaItem = TriviaSet.get(gameData.currentId);

    return (
    <>
        <Stack direction="row" spacing={4} sx={{margin: 3, display:"flex", justifyContent:"space-between"}}>
            <IconButton onClick={onClickHome}>
                <HomeIcon htmlColor="dimgray"/>
            </IconButton>
            <TriviaScore team1Points={gameData.team1Points} team2Points={gameData.team2Points}></TriviaScore>
        </Stack>
        {gameData.hasWon ? 
            <>
            <Confetti width={width} height={height} />
            { Number(gameData.team1Points) > Number(gameData.team2Points) && <Typography variant="h2" gutterBottom color="darkseagreen" style={{padding: "20px"}}>Team 1 Wins!</Typography>}
            { Number(gameData.team1Points) < Number(gameData.team2Points) && <Typography variant="h2" gutterBottom color="indianred" style={{padding: "20px"}}>Team 2 Wins!</Typography>}
            { Number(gameData.team1Points) === Number(gameData.team2Points) && <Typography variant="h2" gutterBottom color="dimgray" style={{padding: "20px"}}>It's a Tie!</Typography>}
            </>
            : 
            <>
            {currentTriviaItem?.type === TriviaType.Category && <TriviaCategory round={(currentTriviaItem as ITriviaCategory)?.round} number={(currentTriviaItem as ITriviaCategory)?.number} description={(currentTriviaItem as ITriviaCategory)?.description} points={(currentTriviaItem as ITriviaCategory)?.points}></TriviaCategory>}
            {currentTriviaItem?.type === TriviaType.Question && <TriviaQuestion round={(currentTriviaItem as ITriviaQuestion)?.round} number={(currentTriviaItem as ITriviaQuestion)?.number} question={(currentTriviaItem as ITriviaQuestion)?.question} points={(currentTriviaItem as ITriviaQuestion)?.points} video={(currentTriviaItem as ITriviaQuestion)?.video} shouldHideVideo={(currentTriviaItem as ITriviaQuestion)?.shouldHideVideo} image={(currentTriviaItem as ITriviaQuestion)?.image}></TriviaQuestion>}
            {currentTriviaItem?.type === TriviaType.Answer && <TriviaAnswer round={(currentTriviaItem as ITriviaAnswer)?.round} number={(currentTriviaItem as ITriviaAnswer)?.number} answer={(currentTriviaItem as ITriviaAnswer)?.answer} points={(currentTriviaItem as ITriviaAnswer)?.points} video={(currentTriviaItem as ITriviaAnswer)?.video} shouldHideVideo={(currentTriviaItem as ITriviaAnswer)?.shouldHideVideo} image={(currentTriviaItem as ITriviaAnswer)?.image}></TriviaAnswer>}
            <Buttons triviaType={TriviaSet.get(gameData.currentId)?.type} onClickBack={gameData.currentId !== "1" ? onClickBack: undefined} onClickNext={onClickNext} onClickAddPoints={onClickAddPoints} ></Buttons>
            </>
        }
    </>
  );
}
