import * as React from 'react';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import { styled } from '@mui/material/styles';


interface IButtonProps {
 canSubmit: boolean;
 onSubmit: () => void;
 onDeselectAll: () => void;
 onShuffle: () => void;
}

const StyledButton = styled(Button)(() => ({
    '&.MuiButton-root': {
        fontSize: 11, 
        color: "rgba(0, 0, 0, 0.54)", 
        border: "1px solid rgba(0, 0, 0, 0.54)"
    }
  }));

export default function Buttons(props: IButtonProps) {
    const { canSubmit, onSubmit, onDeselectAll, onShuffle } = props;
    return (
        <Stack direction="row" spacing={2} sx={{marginTop: 3, display:"flex", justifyContent:"center"}}>
        <StyledButton variant="outlined" sx={{fontSize: 11, color: "rgba(0, 0, 0, 0.54)", border: "1px solid rgba(0, 0, 0, 0.54)"}} onClick={onShuffle}>Shuffle</StyledButton>
        <StyledButton variant="outlined" sx={{fontSize: 11, color: "rgba(0, 0, 0, 0.54)", border: "1px solid rgba(0, 0, 0, 0.54)"}} onClick={onDeselectAll}>Deselect All</StyledButton>
        <Button variant="outlined" sx={{fontSize: 11, color: "rgba(0, 0, 0, 0.54)", border: "1px solid rgba(0, 0, 0, 0.54)"}} disabled={!canSubmit} onClick={onSubmit}>
            Submit
        </Button>
        </Stack>
    );
}
