import * as React from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';
import GridCard from './word-game-components/GridCard';
import { IWord, Words } from '../data/Words';
import Buttons from './word-game-components/Buttons'; 
import GridCategory from './word-game-components/GridCategory';
import Alert from '@mui/material/Alert';
import Confetti from 'react-confetti';
import {useWindowSize} from 'react-use';
import Mistakes from './word-game-components/Mistakes';
import { IconButton, Stack } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';

export interface IWordGameProps {
    onClickHome: () => void;
};

interface IGameData {
    selectedWords: Set<IWord>;
    remainingWords: Array<IWord>;
    solvedCategories: string[];
    submissionResult: string | undefined;
    hasWon: boolean;
    numMistakesLeft: number;
}

export default function WordGame(props: IWordGameProps) {
    const { onClickHome } = props;
    const {width, height} = useWindowSize();
    const [gameData, setGameData] = React.useState<IGameData>({
        selectedWords: new Set<IWord>(),
        remainingWords: Array.from(Words),
        solvedCategories: [],
        submissionResult: undefined,
        hasWon: false,
        numMistakesLeft: 5
    })

    const onGridCardSelected = (word: IWord) => {
        if ((!gameData.selectedWords || !gameData.selectedWords.has(word)) && !(gameData.selectedWords.size >= 4)) {
            setGameData(
                {
                    selectedWords: new Set(gameData.selectedWords).add(word),
                    remainingWords: gameData.remainingWords,
                    solvedCategories: gameData.solvedCategories,
                    submissionResult: gameData.submissionResult,
                    hasWon: gameData.hasWon,
                    numMistakesLeft: gameData.numMistakesLeft
                }
            );
            return;
        }

        const newSelectedWords = new Set(gameData.selectedWords);
        newSelectedWords.delete(word);
        setGameData(
            {
                selectedWords: newSelectedWords,
                remainingWords: gameData.remainingWords,
                solvedCategories: gameData.solvedCategories,
                submissionResult: undefined,
                hasWon: gameData.hasWon,
                numMistakesLeft: gameData.numMistakesLeft
            }
        );
    }

    const onShuffle = () => {
        const newRemainingWords = gameData.remainingWords.sort(() => Math.random() - 0.5); 
        setGameData(
            {
                selectedWords: gameData.selectedWords,
                remainingWords: newRemainingWords,
                solvedCategories: gameData.solvedCategories,
                submissionResult: gameData.submissionResult,
                hasWon: gameData.hasWon,
                numMistakesLeft: gameData.numMistakesLeft
            }
        );
    }

    const onDeselectAll = () => {
        const newSelectedWords = new Set<IWord>();
        setGameData(
            {
                selectedWords: newSelectedWords,
                remainingWords: gameData.remainingWords,
                solvedCategories: gameData.solvedCategories,
                submissionResult: undefined,
                hasWon: gameData.hasWon,
                numMistakesLeft: gameData.numMistakesLeft
            }
        );
    }

    const canSubmit = () => !!(gameData.selectedWords && gameData.selectedWords.size === 4);

    const onSubmit = () => {
        const submittedCategoryCount = new Map();
        if (gameData.selectedWords) {
            Array.from(gameData.selectedWords).forEach(word => {
                const existingEntry = submittedCategoryCount.get(word.category);
                if (existingEntry) {
                    submittedCategoryCount.set(word.category, existingEntry+1)
                } else {
                    submittedCategoryCount.set(word.category, 1)
                }
            });

            if (submittedCategoryCount.size === 1) {
                // update remaining words
                const newRemainingWords = new Set(gameData.remainingWords);
                Array.from(gameData.selectedWords).forEach(word => {
                    newRemainingWords.delete(word);
                });

                // update solved categories
                const newSolvedCategories = gameData.solvedCategories;
                newSolvedCategories.push(Array.from(submittedCategoryCount.keys())[0]);

                // update selected set
                const newSelectedWords = new Set<IWord>();

                // update submission result
                const newSubmissionResult = "You got it!"

                // you won!
                let newHasWon = false;
                if (newSolvedCategories.length === 4) {
                    newHasWon = true;
                }

                setGameData(
                    {
                        selectedWords: newSelectedWords,
                        remainingWords: Array.from(newRemainingWords),
                        solvedCategories: newSolvedCategories,
                        submissionResult: newSubmissionResult,
                        hasWon: newHasWon,
                        numMistakesLeft: gameData.numMistakesLeft
                    }
                );
            }

            else {
                if (gameData.numMistakesLeft === 1) {
                    // update remaining words
                    const newRemainingWords = new Array<IWord>();

                    // update solved categories
                    const solvedCategories = gameData.solvedCategories;
                    const unsolvedCategories: string[] = [];
                    Array.from(Words).forEach(word => {
                        if (!solvedCategories.includes(word.category) && !unsolvedCategories.includes(word.category)) {
                            unsolvedCategories.push(word.category);
                        }
                    });
                    const newSolvedCategories = solvedCategories.concat(unsolvedCategories);

                    // update selected set
                    const newSelectedWords = new Set<IWord>();

                    // update submission result
                    const newSubmissionResult = "Game over :("

                    setGameData(
                        {
                            selectedWords: newSelectedWords,
                            remainingWords: newRemainingWords,
                            solvedCategories: newSolvedCategories,
                            submissionResult: newSubmissionResult,
                            hasWon: gameData.hasWon,
                            numMistakesLeft: gameData.numMistakesLeft-1
                        }
                    );
                }

                else if (submittedCategoryCount.size === 2) {
                    const counts = Array.from(submittedCategoryCount.values());
                    if (counts.includes(1) && counts.includes(3)) {
                        // update submission result
                        const newSubmissionResult = "One away!"
    
                        setGameData(
                            {
                                selectedWords: gameData.selectedWords,
                                remainingWords: gameData.remainingWords,
                                solvedCategories: gameData.solvedCategories,
                                submissionResult: newSubmissionResult,
                                hasWon: gameData.hasWon,
                                numMistakesLeft: gameData.numMistakesLeft-1
                            }
                        );
                    } else {
                        // update submission result
                        const newSubmissionResult = "Nice try!"
    
                        setGameData(
                            {
                                selectedWords: gameData.selectedWords,
                                remainingWords: gameData.remainingWords,
                                solvedCategories: gameData.solvedCategories,
                                submissionResult: newSubmissionResult,
                                hasWon: gameData.hasWon,
                                numMistakesLeft: gameData.numMistakesLeft-1
                            }
                        );
                    }
                }
    
                else {
                    // update submission result
                    const newSubmissionResult = "Nice try!"
    
                    setGameData(
                        {
                            selectedWords: gameData.selectedWords,
                            remainingWords: gameData.remainingWords,
                            solvedCategories: gameData.solvedCategories,
                            submissionResult: newSubmissionResult,
                            hasWon: gameData.hasWon,
                            numMistakesLeft: gameData.numMistakesLeft-1
                        }
                    );
                }
            }
        }
    }

    return (
    <>
        <Stack direction="row" spacing={4} sx={{margin: 3, display:"flex", justifyContent:"start"}}>
            <IconButton onClick={onClickHome}>
                <HomeIcon htmlColor="dimgray"/>
            </IconButton>
        </Stack>
        <Box sx={{display:"flex", justifyContent:"center", margin: 2}}>
        <Grid container spacing={2} columns={4} sx={{width: 330, height: 330}}>
            {gameData.solvedCategories.map((category, index) => (
            <Grid xs={4} key={index}>
                <GridCategory category={category}></GridCategory>
            </Grid>
            ))}
            {gameData.remainingWords.map((word, index) => (
            <Grid xs={1} key={index}>
                <GridCard word={word} isSelected={!!(gameData.selectedWords && gameData.selectedWords.has(word))} onSelected={onGridCardSelected}></GridCard>
            </Grid>
            ))}
        </Grid>
        </Box>
        <Mistakes numMistakesLeft={gameData.numMistakesLeft}></Mistakes>
        <Buttons canSubmit={canSubmit()} onSubmit={onSubmit} onDeselectAll={onDeselectAll} onShuffle={onShuffle}></Buttons>
        <Box sx={{display:"inline-block", marginTop: 3, justifyContent:"center", width: 330}}>
        {(gameData.submissionResult === "Game over :(") ? <Alert severity="error">{gameData.submissionResult}</Alert> 
                : (gameData.submissionResult === "You got it!") ? <Alert severity="success">{gameData.submissionResult}</Alert> 
                : (gameData.submissionResult === "One away!") ?  <Alert severity="info">{gameData.submissionResult} {gameData.numMistakesLeft} {gameData.numMistakesLeft >1 ? "mistakes" : "mistake"} remaining</Alert> 
                : (gameData.submissionResult === "Nice try!") ? <Alert severity="error">{gameData.submissionResult} {gameData.numMistakesLeft} {gameData.numMistakesLeft >1 ? "mistakes" : "mistake"} remaining</Alert> 
                : undefined}
        </Box>
        {gameData.hasWon ? <Confetti width={width} height={height} /> : undefined}
    </>
  );
}
