import * as React from 'react';
import Stack from '@mui/material/Stack';
import { Typography } from '@mui/material';


interface IButtonProps {
 team1Points: string;
 team2Points: string;
}


export default function TriviaScore(props: IButtonProps) {
    const { team1Points, team2Points } = props;
    return (
        <Stack direction="row" spacing={4} sx={{marginTop: 3, marginRight: 3, marginBottom: 3, display:"flex", justifyContent:"end"}}>
            <Typography variant="h5" gutterBottom color="darkseagreen">
            Team 1: {team1Points}
            </Typography>
            <Typography variant="h5" gutterBottom color="indianred">
            Team 2: {team2Points}
            </Typography>
        </Stack>
    );
}
