import * as React from 'react';
import { Box, Typography } from '@mui/material';

export interface ITriviaAnswerProps {
    round?: string;
    number?: string;
    answer?: string;
    points?: string;
    video?: string;
    shouldHideVideo?: boolean;
    image?: string;
}

export default function TriviaAnswer(props: ITriviaAnswerProps) {
    const { round, number, answer, points, video, shouldHideVideo, image} = props;
    return (
        <Box>
        <Typography variant="h2" gutterBottom color="whitesmoke" style={{background: "indianred", margin: "20px", borderRadius: "30px", padding: "20px"}}>
            {round}
        </Typography>
        <Typography variant="h4" gutterBottom color="dimgray" style={{padding: "40px"}}>
            #{number}: {answer}
        </Typography>
        <Typography variant="h5" gutterBottom color="indianred">
            {points}
        </Typography>
        {video && 
            <iframe allow="autoplay" style={{display: shouldHideVideo ? "none" : "auto"}} width="420" height="315" src={video}>
            </iframe>
        }
        {image && 
            <img src={image} style={{maxHeight: "225px", maxWidth: "-webkit-fill-available"}}>
            </img>
        }
        </Box>
    );
}
