import * as React from 'react';
import { Box, Typography } from '@mui/material';

export interface ITriviaCategoryProps {
    round?: string;
    number?: string;
    description?: string;
    points?: string;
}

export default function TriviaCategory(props: ITriviaCategoryProps) {
    const { round, number, description, points } = props;
    return (
        <Box sx={{background: Number(number) % 2 === 0 ? "indianred" : "darkseagreen", padding: "40px", margin: "20px", borderRadius: "30px", }} color="whitesmoke">
        <Typography variant="h2" gutterBottom> 
            {round}
        </Typography>
        <Typography variant="h4" gutterBottom style={{padding: "40px"}}>
            {description}
        </Typography>
        <Typography variant="h5" gutterBottom style={{paddingTop: "15px"}}>
            {points}
        </Typography>
        </Box>
    );
}
