import React from 'react';
import './App.css';
import WordGame from './components/WordGame';
import TriviaGame from './components/TriviaGame';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';

enum SelectedGame {
  WordGame = 1,
  TriviaGame,
}


function App() {
  const [selectedGame, setSelectedGame] = React.useState<SelectedGame | undefined>(undefined);

  const onClickWordGame = () => {
    setSelectedGame(SelectedGame.WordGame);
  };
  
  const onClickTriviaGame = () => {
    setSelectedGame(SelectedGame.TriviaGame);
  };

  const onClickHome = () => {
    setSelectedGame(undefined);
  };

  return (
    <div className="App">
      {!selectedGame &&
      <Stack direction="row" spacing={2} sx={{marginTop: 3, display:"flex", justifyContent:"center"}}>
        <Button variant="outlined" sx={{fontSize: 11, color: "rgba(0, 0, 0, 0.54)", border: "1px solid rgba(0, 0, 0, 0.54)"}} onClick={onClickWordGame}>
            Word Game
        </Button>
        <Button variant="outlined" sx={{fontSize: 11, color: "rgba(0, 0, 0, 0.54)", border: "1px solid rgba(0, 0, 0, 0.54)"}} onClick={onClickTriviaGame}>
            Trivia Game
        </Button>
        </Stack>
      }
      {selectedGame === SelectedGame.WordGame &&  <WordGame onClickHome={onClickHome}/> }
      {selectedGame === SelectedGame.TriviaGame && <TriviaGame onClickHome={onClickHome}/>}
    </div>
  );
}

export default App;
