import * as React from 'react';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import { Box, TextField } from '@mui/material';
import { TriviaType } from '../../data/Trivia';


interface IButtonProps {
 triviaType: TriviaType | undefined;
 onClickNext: () => void;
 onClickAddPoints: (team1Points: string, team2Points: string) => void;
 onClickBack?: () => void;
}


export default function Buttons(props: IButtonProps) {
    const [team1Points, setTeam1Points] = React.useState<string>("");
    const [team2Points, setTeam2Points] = React.useState<string>("");
    const { triviaType, onClickBack, onClickNext, onClickAddPoints } = props;
    
    const onAddPoints = () => {
        onClickAddPoints(team1Points, team2Points);
        setTeam1Points("");
        setTeam2Points("");
    }

    return (
            <Box sx={{ paddingLeft: "20px", paddingRight: "20px", paddingBottom: "20px" }}>

                {triviaType === TriviaType.Answer &&
                <Stack direction="row" spacing={2} sx={{marginTop: 3, display:"flex", justifyContent:"center"}}>
                <TextField
                    id="filled-number-1"
                    label="Team 1 Points"
                    type="number"
                    variant="standard"
                    value={team1Points}
                    onInput={e => setTeam1Points((e.target as HTMLInputElement).value)}
                />
                <TextField
                    id="filled-number-2"
                    label="Team 2 Points"
                    type="number"
                    variant="standard"
                    value={team2Points}
                    onInput={e => setTeam2Points((e.target as HTMLInputElement).value)}
                />
                <Button variant="outlined" sx={{fontSize: 11, color: "dimgray", border: "1px solid dimgray"}} onClick={onAddPoints}>Add Points</Button>
                </Stack>
                }
                <Stack direction="row" spacing={2} sx={{marginTop: 3, display:"flex", justifyContent:"center"}}>
                { onClickBack ? <Button variant="outlined" sx={{fontSize: 11, color: "dimgray", border: "1px solid dimgray"}} onClick={onClickBack}>
                    Back
                </Button> : undefined}
                <Button variant="outlined" sx={{fontSize: 11, color: "dimgray", border: "1px solid dimgray"}} onClick={onClickNext}>
                    Next
                </Button>
                </Stack>
            </Box>
    );
}
